<template>
    <div class="regiment">
        <!--  -->
        <!-- <van-nav-bar title="" left-text="" left-arrow @click-left="onClickLeft" /> -->
        <header class="header">
            <img src="../../../../static/img/header.jpg" alt="">
        </header>
        <!-- main -->
        <main class="main">
            <!--  -->
            <section class="detail-1">
                <div class="">
                    <h1>5</h1>
                </div>
                <div class="detail-content">
                    <p class="">
                        <span></span>
                        <span id="activityTime">2019.06.01 - 2019.06.30 </span>
                    </p>

                </div>
            </section>
            <!--  -->
            <section class="hot">
                <h2></h2>
                <div class="common-box">
                    <img src="../../../../static/img/zhibo-zanshi.jpg" alt="">
                    <img src="../../../../static/img/zhibo-zanshi2.jpg" alt="">
                    <img src="../../../../static/img/zhibo-zanshi3.jpg" alt="">
                    <img src="../../../../static/img/zhibo-zanshi.jpg" alt="">
                    <img src="../../../../static/img/zhibo-zanshi2.jpg" alt="">
                    <img src="../../../../static/img/zhibo-zanshi3.jpg" alt="">
                    <img src="../../../../static/img/zhibo-zanshi.jpg" alt="">
                    <img src="../../../../static/img/zhibo-zanshi2.jpg" alt="">
                    <img src="../../../../static/img/zhibo-zanshi3.jpg" alt="">
                    <img src="../../../../static/img/zhibo-zanshi.jpg" alt="">
                    <img src="../../../../static/img/zhibo-zanshi2.jpg" alt="">
                    <img src="../../../../static/img/zhibo-zanshi3.jpg" alt="">
                    <img src="../../../../static/img/zhibo-zanshi2.jpg" alt="">
                    <img src="../../../../static/img/zhibo-zanshi3.jpg" alt="">
                    <img src="../../../../static/img/zhibo-zanshi.jpg" alt="">
                    <img src="../../../../static/img/zhibo-zanshi2.jpg" alt="">
                    <img src="../../../../static/img/zhibo-zanshi3.jpg" alt="">
                    <img src="../../../../static/img/zhibo-zanshi.jpg" alt="">
                    <img src="../../../../static/img/zhibo-zanshi2.jpg" alt="">
                    <img src="../../../../static/img/zhibo-zanshi3.jpg" alt="">
                    <img src="../../../../static/img/zhibo-zanshi.jpg" alt="">
                    <img src="../../../../static/img/zhibo-zanshi2.jpg" alt="">
                    <img src="../../../../static/img/zhibo-zanshi3.jpg" alt="">
                </div>
            </section>
            <!--  -->
            <section class="list">
                <h2></h2>
                <!--  -->
                <ul v-if="isShow">
                    <li v-for="(item, index) in aliveList" :key="index" @click="toZongbu(item)">
                        {{item.name}}
                    </li>

                </ul>
                <button @click="scenePhoto"></button>
            </section>

        </main>
        <button class="upload"></button>

    </div>
</template>
<script>
export default {
    name: 'regiment',
    data() {
        return {
            isShow: false, //
            aliveList: [
                {
                    // id:1,//id
                    name: '',
                },
                {
                    name: '',
                },
                {
                    name: '',
                },
                {
                    name: '',
                },
                {
                    name: '',
                },
                {
                    name: '',
                },
                {
                    name: '',
                },
            ],
        }
    },
    methods: {
        // 
        toZongbu(item) {
            // :liveRegiment?id=item.name
            this.$router.push({ path: 'liveRegiment', query: { id: item.id } })
        },
        // 
        scenePhoto(){
            this.$router.push({path:'wholeCountry'});
        },
        // 
        onClickLeft() {
            this.$router.go(-1)
        },
    },
}
</script>
<style lang="scss" scoped>
.regiment{
    background-color: #ecf1f3;
}
.van-nav-bar .van-icon {
    color: #000;
}
.van-nav-bar__title {
    font-size: 0.9rem;
}
.header img {
    display: block;
    width: 100%;
    height: 100%;
}
.main {
    // background-color: #111;
    
    // background: url("../../../../static/img/zhibo-bg@2x.png") no-repeat center top;
    // color: #fff;
    color: #44559b;
    padding: 0.8rem;
    .detail-1 {
        text-align: left;
        div {
            h1 {
                font-size: 1rem;
            }
        }
        .detail-content {
            font-size: 0.6rem;
            // color: #ccc;
            color: #44559b;
        }
    }
    .hot {
        border-top: 1px solid #ccc;

        h2 {
            // color: #fff;
            color: #44559b;
            font-size: 1rem;
            line-height: 1;
            text-align: left;
        }
        .common-box {
            width: 100%;
            margin: 0.32rem 0 0 0;
            overflow: auto;
            display: flex;
            img {
                width: 76px;
                height: 76px;
                -webkit-box-flex: 0;
                -ms-flex: none;
                flex: none;
                background-position: top;
                background-size: cover;
                margin: 0 0.2rem 0 0;
                background-color: #ccc;
            }
        }
    }
    .list {
        border-top: 1px solid #ccc;
        margin-top: 0.94rem;
        h2 {
            // color: #fff;
            color: #44559b;
            font-size: 1rem;
            line-height: 1;
            text-align: left;
        }
        ul {
            li {
                height: 3.75rem;
                margin-top: 1rem;
                height: 3.75rem;
                line-height: 3.75rem;
            }
        }
        button {
            width: 100%;
            height: 3rem;
            font-size: 1rem;
            color: #ffffff;
            line-height: 3rem;
            background-color: #487dbb;
            border-radius: 10px;
        }
    }
}

.list ul li:nth-child(odd) {
    background-color: #013d9c;
}
.list ul li:nth-child(even) {
    background-color: #ffffff;
    color: #3586ff;
}
.upload {
    width: 100%;
    height: 3rem;
    font-size: 1rem;
    color: #ffffff;
    line-height: 3rem;
    background-color: #487dbb;
    margin-top: 1.75rem;
 
}
</style>



